import { useState } from "react";
import { useParams } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const Editpost = ({ postInEdit, hanldeAddPost }) => {
  console.log(postInEdit);
  console.log(hanldeAddPost);
  const { id } = useParams();
  const [title, setTitle] = useState(postInEdit.title);
  const [body, setBody] = useState(postInEdit.body);
  const [imgURL, setImgURL] = useState(postInEdit.imgURL);
  const [date, setDate] = useState("");

  const navigate = useNavigate();

  const checkDataBeforeSend = () => {
    if (title && body && imgURL) {
      const newdate = new Date().toLocaleString();
      hanldeAddPost(title, body, imgURL, newdate);
      navigate("/", { replace: true });
      return;
    }
  };

  const editData = () => {
    axios
      .put(`https://62cb57951eaf3786ebb8210b.mockapi.io/article/${id}`, {
        title,
        body,
        imgURL,
        date,
      })
      .then(() => {
        navigate("/", { replace: true });
      });
  };

  return (
    <>
      <div className="col-10 mx-auto">
        <div class="form-group pt-5">
          <label class="col-form-label mt-4" htmlFor="inputDefault">
            Post title
          </label>
          <input
            type="text"
            class="form-control"
            id="inputDefault"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
          />
        </div>
        <div class="form-group">
          <label htmlFor="exampleTextarea" class="form-label mt-4">
            Post
          </label>
          <textarea
            class="form-control"
            id="exampleTextarea"
            rows="3"
            placeholder="Post body"
            value={body}
            onChange={(e) => setBody(e.target.value)}
          ></textarea>
          <div class="form-group">
            <label for="formFile" class="form-label mt-4">
              Image URL
            </label>
            <input
              class="form-control"
              type="text"
              id="formFile"
              value={imgURL}
              onChange={(e) => setImgURL(e.target.value)}
            />
          </div>
          <div className="d-flex flex-row-reverse">
            <input type="button" className="btn btn-success mt-2 ml-auto" value="Save Edit" onClick={editData} />
          </div>
        </div>
      </div>
    </>
  );
};

export default Editpost;
