import { useState } from "react";
import { Link } from "react-router-dom";
import Pagination from "../components/Pagination";
import Post from "../components/Post";
import Sidebar from "../components/Sidebar";

const Home = ({ posts, handleDeletePost, handleEditPost, authTest, handleSearch, search }) => {
  return (
    <>
      <div className="container d-flex justify-content-center mb-0">
        <div className=" d-flex col-7  pt-4 flex-row-reverse">
          {authTest === true ? (
            <Link to="/addpost">
              <input type="button" className="btn btn-success mb-3 ml-auto" value="New Post" />
            </Link>
          ) : (
            <></>
          )}
        </div>
        <div className="col-3"></div>
      </div>
      <div className="container d-flex justify-content-center ">
        <div className={authTest === true ? "col-7 m-4 mt-0" : "col-7 m-4 mt-3"}>
          <Post
            posts={posts}
            authTest={authTest}
            handleDeletePost={handleDeletePost}
            handleEditPost={handleEditPost}
            search={search}
          />
        </div>
        <div className="col-3 ml-4 mt-0">
          <Sidebar handleSearch={handleSearch} search={search} />
        </div>
      </div>
    </>
  );
};

export default Home;
