import { useState } from "react";

const Login = ({ handleSignIn }) => {
  const [usrName, setUsrName] = useState("");
  const [usrPassword, setUsrPasswrod] = useState("");
  return (
    <>
      <div className="col-4 mx-auto">
        <div class="form-group pt-5">
          <label class="col-form-label mt-4" htmlFor="inputDefault">
            Email
          </label>
          <input
            type="text"
            class="form-control"
            placeholder="Email"
            id="inputDefault"
            value={usrName}
            onChange={(e) => setUsrName(e.target.value)}
          />
        </div>
        <div class="form-group">
          <label htmlFor="exampleTextarea" class="form-label mt-2">
            Password
          </label>
          <input
            type="password"
            class="form-control"
            placeholder="Password"
            id="inputDefault"
            value={usrPassword}
            onChange={(e) => setUsrPasswrod(e.target.value)}
          />
          <div class="form-group"></div>
          <div className="d-flex flex-row-reverse">
            <input
              type="button"
              className="btn btn-success mt-2 ml-auto"
              value="Login"
              onClick={() => handleSignIn(usrName, usrPassword)}
            />
          </div>
        </div>
      </div>
    </>
  );
};

export default Login;
