import { Link, Navigate, useNavigate } from "react-router-dom";
import axios from "axios";
import { useEffect, useState } from "react";
import { toast } from "react-toastify";

const Post = ({ posts, handleDeletePost, handleEditPost, authTest, search }) => {
  const [APIData, setAPIData] = useState([]);

  const navigate = useNavigate();

  useEffect(() => {
    axios.get(`https://62cb57951eaf3786ebb8210b.mockapi.io/article`).then((response) => {
      console.log(response.data);
      setAPIData(response.data);
    });
  }, []);

  const deletePost = (id) => {
    axios.delete(`https://62cb57951eaf3786ebb8210b.mockapi.io/article/${+id}`).then(() => {
      console.log("ok" + id);

      axios.get(`https://62cb57951eaf3786ebb8210b.mockapi.io/article`).then((response) => {
        setAPIData(response.data);
      });
    });
    toast.success(`Post No.${id} deleted`);
  };
  const searchedPosts = APIData.filter((p) => p.title.toLowerCase().includes(search.toLowerCase()));

  //   --------- Pagination ---------
  const itemsCountPerPage = 10;
  const [currentPage, setCurrentPage] = useState(1);

  //   Get the start index in the page
  const startIndexInPage = (currentPage - 1) * itemsCountPerPage;

  //   Get the items in the page
  const paginatdItems = searchedPosts.slice(startIndexInPage, startIndexInPage + itemsCountPerPage);

  //   Get no of pages
  const noOfPages = Math.ceil(searchedPosts.length / itemsCountPerPage);

  //   Get array of pages numbers
  let pagesArray = [];
  for (let i = 1; i <= noOfPages; i++) {
    pagesArray.push(i);
  }

  //   Change Page handler
  const handleChangePage = (page) => {
    setCurrentPage(page);
    window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
  };

  return (
    <>
      {paginatdItems.map((post) => (
        <div
        // onClick={() => {
        //   showPost(post.id);
        // }}
        >
          <div class="card border-primary mb-3 mt-0">
            <div className="d-flex">
              <div className="col-8 pr-1 px-0">
                <div class="card-header d-flex justify-content-between">
                  <div>
                    <h5>{post.title}</h5>
                  </div>
                  <div className="d-flex flex-row-reverse">
                    {authTest === true ? (
                      <>
                        <i
                          class=""
                          style={{ width: 30, height: 30, fontSize: 15, padding: 3, fontWeight: 100 }}
                          className="btn btn-success fa fa-pencil mx-1"
                          onClick={() => handleEditPost(post)}
                        ></i>
                        <i
                          class=""
                          style={{ width: 30, height: 30, fontSize: 15, padding: 3, fontWeight: 100 }}
                          className="btn btn-danger fa fa-trash "
                          onClick={() => deletePost(post.id)}
                        ></i>
                      </>
                    ) : (
                      <></>
                    )}
                  </div>
                </div>

                <p style={{ fontSize: 13 }} className="m-1 d-flex flex-row-reverse">
                  {post.date}
                </p>
                <div class="card-body p-1">
                  <p class="card-text">
                    {post.body.substring(0, 150)}...
                    <span>
                      <Link to={"/post/" + post.id}>Read more</Link>
                    </span>
                  </p>
                </div>
              </div>
              <div className="p-0 ">
                <div className="overflow-hidden">
                  <Link to={"/post/" + post.id}>
                    <img className="img-fluid" style={{ objectFit: "cover" }} src={post.imgURL} />
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      ))}
      {pagesArray.length !== 1 && (
        <nav aria-label="...">
          <ul className="pagination">
            {pagesArray.map((page) => (
              <li
                key={page}
                style={{ cursor: "pointer" }}
                className={page === currentPage ? "page-item active" : "page-item"}
                aria-current="page"
                onClick={() => handleChangePage(page)}
              >
                <span className="page-link">{page}</span>
              </li>
            ))}
          </ul>
        </nav>
      )}
    </>
  );
};

export default Post;
