import { useNavigate, useParams } from "react-router-dom";
import axios from "axios";
import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { toast } from "react-toastify";

const ShowPost = ({ authTest, handleEditPost }) => {
  const { id } = useParams();
  // const selectedPost = posts.filter((p) => p.id === +id);

  const [APIData, setAPIData] = useState("");

  const navigate = useNavigate();

  useEffect(() => {
    console.log(APIData);
    axios.get(`https://62cb57951eaf3786ebb8210b.mockapi.io/article/${id}`).then((response) => {
      console.log(response.data);
      setAPIData(response.data);
    });
  }, []);

  const deletePost = (id) => {
    axios.delete(`https://62cb57951eaf3786ebb8210b.mockapi.io/article/${+id}`).then(() => {
      console.log("ok" + id);

      axios.get(`https://62cb57951eaf3786ebb8210b.mockapi.io/article`).then((response) => {
        setAPIData(response.data);
      });
      navigate(`/`, { replace: true });
      toast.success("Post deleted");
    });
  };
  return (
    <>
      {APIData === "" ? (
        <></>
      ) : (
        <div
        // onClick={() => {
        //   showPost(post.id);
        // }}
        >
          <div class=" mb-3 mt-0 pt-5">
            <div className="d-flex">
              <div className="col-8 pr-1 px-0">
                <div class="card-header d-flex justify-content-between">
                  <div>
                    <h5>{APIData.title} </h5>
                  </div>
                  <div className="d-flex flex-row-reverse">
                    {authTest === true ? (
                      <>
                        <i
                          class=""
                          style={{ width: 30, height: 30, fontSize: 15, padding: 3, fontWeight: 100 }}
                          className="btn btn-success fa fa-pencil mx-1"
                          onClick={() => handleEditPost(APIData)}
                        ></i>
                        <i
                          class=""
                          style={{ width: 30, height: 30, fontSize: 15, padding: 3, fontWeight: 100 }}
                          className="btn btn-danger fa fa-trash "
                          onClick={() => deletePost(APIData.id)}
                        ></i>
                      </>
                    ) : (
                      <></>
                    )}
                  </div>
                </div>

                <p style={{ fontSize: 13 }} className="m-1 d-flex flex-row-reverse">
                  {APIData.date}
                </p>
                <div class="card-body p-1">
                  <p class="card-text">{APIData.body}</p>
                </div>
              </div>
              <div className="p-0 ">
                <div className="overflow-hidden">
                  <Link to={"/post/" + APIData.id}>
                    <img className="img-fluid" style={{ objectFit: "cover" }} src={APIData.imgURL} />
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default ShowPost;
