import { Link } from "react-router-dom";

const Header = ({ handleAction, authTest, handleLogout }) => {
  return (
    <>
      <nav class="navbar navbar-expand-lg navbar-dark bg-primary fixed-top">
        <div class="container-fluid">
          <Link class="navbar-brand" to="/">
            Blog
          </Link>
          <button
            class="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarColor01"
            aria-controls="navbarColor01"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span class="navbar-toggler-icon"></span>
          </button>

          <div class="collapse navbar-collapse" id="navbarColor01">
            <ul class="navbar-nav me-auto">
              <li class="nav-item">
                <Link class="nav-link" to={"/"}>
                  Home
                  <span class="visually-hidden">(current)</span>
                </Link>
              </li>

              <li class="nav-item">
                <Link class="nav-link" to={"/addpost"}>
                  New Post
                </Link>
              </li>
              <li class="nav-item">
                <Link class="nav-link" to={"/About"}>
                  About
                  <span class="visually-hidden">(current)</span>
                </Link>
              </li>
            </ul>
            <form class="d-flex" abineguid="0A5FFF10BDDA44F786BDBDAFC3FCA9D0">
              {authTest == true ? (
                <button class="btn btn-secondary my-2 my-sm-0" type="submit" onClick={handleLogout}>
                  Logout
                </button>
              ) : (
                <Link to="/login">
                  <button class="btn btn-secondary my-2 my-sm-0" type="submit">
                    Admin Login
                  </button>
                </Link>
              )}
            </form>
          </div>
        </div>
      </nav>
    </>
  );
};

export default Header;
