import { useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { toast } from "react-toastify";

const Addpost = ({ hanldeAddPost }) => {
  const [title, setTitle] = useState("");
  const [body, setBody] = useState("");
  const [imgURL, setImgURL] = useState("");

  const navigate = useNavigate();

  const postData = () => {
    const newdate = new Date().toLocaleString();

    console.log(newdate);
    axios
      .post(`https://62cb57951eaf3786ebb8210b.mockapi.io/article`, {
        title,
        body,
        imgURL,
        date: newdate,
      })
      .then(() => {
        navigate("/", { replace: true });
      });
    toast.success("Post added");
  };

  // const checkDataBeforeSend = () => {
  //   if (title && body && imgURL) {
  //     const newdate = new Date().toLocaleString();
  //     hanldeAddPost(title, body, imgURL, newdate);
  //     navigate("/", { replace: true });
  //     return;
  //   }
  // };

  return (
    <div className="col-10 mx-auto">
      <div class="form-group pt-5">
        <label class="col-form-label mt-4" htmlFor="inputDefault">
          Post title
        </label>
        <input
          type="text"
          class="form-control"
          placeholder="Post title"
          id="inputDefault"
          onChange={(e) => setTitle(e.target.value)}
        />
      </div>
      <div class="form-group">
        <label htmlFor="exampleTextarea" class="form-label mt-4">
          Post
        </label>
        <textarea
          class="form-control"
          id="exampleTextarea"
          rows="3"
          placeholder="Post body"
          onChange={(e) => setBody(e.target.value)}
        ></textarea>
        <div class="form-group">
          <label for="formFile" class="form-label mt-4">
            Image URL
          </label>
          <input class="form-control" type="text" id="formFile" onChange={(e) => setImgURL(e.target.value)} />
        </div>
        <div className="d-flex flex-row-reverse">
          <input
            type="button"
            className="btn btn-success mt-2 ml-auto"
            value="Post"
            onClick={() => {
              postData();
            }}
          />
        </div>
      </div>
    </div>
  );
};

export default Addpost;
